<template>
  <div class="pm-page" :class="[sidebarHiding == true ? 'pm-page-none-sidbar' : 'pm-page']">
    <toolbar
      :pageSubName="this.$store.state.currentPageName"
      :pageSubInnerName="this.$store.state.currentPageInnerName"
      @refreshInfo="refreshPage()"
      :isBackPath="true"
      :isBack_specificPath="'/pipeline'"
      :isNewBtn="false"
      newBtnLabel="New Line"
      @newBtnFn="TOGGLE_POPUPADD()"
      style="grid-column: span 3"
      :infoPipeline="infoPipeline"
      :isMoreBtn="true"
      :isSearchBox="false"
      :isRefresh="true"
      :isSearchDropdown="true"
      :options="pipelineList"
    />
    <sidebar @resizeGridLayout="RESIZE_GRID_LAYOUT()" />
    <div class="pm-page-container">
      <router-view></router-view>
    </div>
    <popupAdd v-if="isAdd == true" @closePopup="TOGGLE_POPUPADD()" />
  </div>
</template> 

<script>
import axios from "/axios.js";
//Structures
import toolbar from "@/components/app-structures/app-navbar-toolbar-pipeline.vue";
import sidebar from "@/components/app-structures/app-sidebar-pipeline.vue";
import popupAdd from "@/views/Applications/FlowLine/info-add.vue";

export default {
  name: "router-template-detail",
  components: {
    toolbar,
    popupAdd,
    sidebar
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_INAPP", {
      name: "PipeLine Management",
      icon: "/img/icon_menu/equipment/pipeline.png"
    });
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "PipeLine",
      subpageInnerName: null
    });
    this.FETCH_INFO();
    this.FETCH_LIST();
  },
  data() {
    return {
      infoPipeline: {},
      pipelineList: [],
      sidebarHiding: false,
      isAdd: false
    };
  },
  computed: {},
  watch: {
    $route: {
      handler() {
        //run logic inside handler whenever $route is change
        this.FETCH_INFO();
      }
      //immediate: true // call the handler immediately when the component is created
    }
  },
  methods: {
    FETCH_INFO() {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      axios({
        method: "get",
        url: "PipelineInfo/" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: null
      })
        .then(res => {
          //console.log(res.data);
          if (res.status == 200 && res.data) {
            this.infoPipeline = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_LIST() {
      this.isLoading = true;
      axios({
        method: "get",
        url: "FlowlineInfo",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: null
      })
        .then(res => {
          // console.log(res);
          if (res.status == 200 && res.data) {
            this.pipelineList = res.data;
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            error.code + " " + error.response.status + " " + error.message
          );
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    RESIZE_GRID_LAYOUT() {
      this.sidebarHiding = !this.sidebarHiding;
    },
    TOGGLE_POPUPADD() {
      this.isAdd = !this.isAdd;
    },
    refreshPage() {
      this.$router.go();
    }
  }
};
</script>

